import axios from "axios"
import shortHash from "short-hash"

async function countingStar() {
  const hashedPathName = shortHash(location.pathname)
  const originPrefix = `https://api.countapi.xyz/hit/${location.hostname}/`
  const originRes = await axios.get(originPrefix)
  console.log(originRes)

  if (location.pathname == "/") return

  const hrefPrefix = `https://api.countapi.xyz/hit/${location.hostname}/${hashedPathName}`
  const hrefRes = await axios.get(hrefPrefix)
  console.log(hrefRes)
}

export default countingStar
